
* {
  margin: 3px 0;
  font-weight: 300;
}
.hello {
  margin: 3em 10%;
  height: 30em;
}
.masthead {
  padding: 0.5em;
  font-weight: 200 !important;
}
.block {
  text-align: left;
  padding-bottom: 1em;
}
.block h4 {
  font-weight: 500;
}
.block h2 {
  font-weight: 500; 
  margin-left: 0;
}
.block h3, .block  {
  font-weight: 500;
}
.block p {
  padding-left: 1em;
  padding-bottom: 0.5em;
}
.exp {
  padding-bottom: 1em;
}
.contact {
  text-align: center;
  padding: 3em;
}
h1, h2, h3, h4 {
  font-weight: 300;
}
h1 {
  font-size: 2em;
}
a {
  color: inherit;
  font-weight: 500;
  text-decoration: none;
}




